import { ListingFormData } from '@shared/types';
import api from './api';

export const listingsApi = {
  // Hotel functions
  getHotelListings: async (status?: string, page = 1, limit = 10) => {
    const params = new URLSearchParams();
    if (status && status !== 'all') params.append('status', status);
    params.append('page', page.toString());
    params.append('limit', limit.toString());

    const response = await api.get(`/listings/hotel?${params}`);
    return response.data;
  },

  createListing: async (data: ListingFormData) => {
    const response = await api.post('/listings', data);
    return response.data;
  },

  updateListing: async (id: string, data: Partial<ListingFormData>) => {
    const response = await api.put(`/listings/${id}`, data);
    return response.data;
  },

  deleteListing: async (id: string) => {
    const response = await api.delete(`/listings/${id}`);
    return response.data;
  },

  // NGO functions
  getAvailableListings: async (
    search?: string,
    foodType?: string,
    page = 1,
    limit = 10,
    city?: string,
    state?: string
  ) => {
    const params = new URLSearchParams();
    if (search) params.append('search', search);
    if (foodType && foodType !== 'all') params.append('foodType', foodType);
    params.append('page', page.toString());
    params.append('limit', limit.toString());
    if (city) params.append('city', city);
    if (state) params.append('state', state);

    const response = await api.get(`/listings/available?${params}`);
    return response.data;
  },

  getNGOClaimedListings: async (page = 1, limit = 10) => {
    const params = new URLSearchParams();
    params.append('page', page.toString());
    params.append('limit', limit.toString());

    const response = await api.get(`/listings/ngo/claimed?${params}`);
    return response.data;
  },

  claimListing: async (id: string) => {
    const response = await api.post(`/listings/${id}/claim`);
    return response.data;
  },

  // Shared functions
  getListingById: async (id: string) => {
    const response = await api.get(`/listings/${id}`);
    return response.data;
  },
};